.App {
  display: flex;
  justify-content: center;
  margin-top: 33vh;
}

body {
  background-color: grey;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  background-color: dodgerblue;
}

#drum-machine {
  position: absolute;
  display: flex;
  flex-direction: row;
  top: 35%;
  border: 3px solid black;
  height: 300px;
  width: 800px;
  background-color: gainsboro;
}

#pads-container {
  position: relative;
  width: 70%;
  height: 100%;
  background-color: lightgray;
  border-right: 2px solid black;
}

#controls-container {
  position: relative;
  height: 100%;
  width: 30%;
  background-color: lightskyblue;
}

#display {
  position: relative;
  margin: auto;
  top: 35%;
  width: 100px;
  height: 25px;
  border: 2px solid black;
  background-color: black;
  color: white;
  justify-content: center;
  text-align: center;
}

.drum-pads {
  position: relative;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  margin: auto;
  margin-top: 15px;
  width: 50%;
  height: 90%;
}

.drum-pad {
  position: relative;
  float: left;
  border: 2px solid black;
  background-color: red;
  color: white;
  border-radius: 15px;
  width: 85px;
  height: 75px;
  display: flex;
  margin: 2.5px;
  justify-content: center;
  align-items: center;
  box-shadow: rgba(14, 30, 37, 0.12) 0px 2px 4px 0px, rgba(14, 30, 37, 0.32) 0px 2px 16px 0px;
}

#title {
  position: relative;
  font-size: 1.35rem;
  padding-left: 25px;
  height: 30px;
  color: red;
  border-bottom: 3px solid black;
}